import React, { useEffect, useState, useCallback } from 'react';
import { Button, Modal, Table } from 'react-bootstrap';
import { db } from '../../../firebase';
import { addDoc, collection, deleteDoc, doc, getDocs, updateDoc } from 'firebase/firestore';
import Swal from 'sweetalert2';

// 🔁 تعريف skillsCollection خارج الكومبوننت (ثابت)
const skillsCollection = collection(db, "skills");

const IndexSkills = () => {
  const [skills, setSkills] = useState([]);
  const [currentSkill, setCurrentSkill] = useState({ id: null, name: "" });
  const [isEdit, setIsEdit] = useState(false);
  const [loading, setLoading] = useState(false);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // ✅ useCallback بدون تحذيرات، لأن skillsCollection ثابت
  const fetchSkills = useCallback(async () => {
    setLoading(true);
    const querySnapshot = await getDocs(skillsCollection);
    const skillsData = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data()
    }));
    setSkills(skillsData);
    setLoading(false);
  }, []);

  // ✅ useEffect الآن لا يحتاج skillsCollection
  useEffect(() => {
    fetchSkills();
  }, [fetchSkills]);

  const addSkill = async () => {
    if (currentSkill.name.trim() === "") return alert("يرجى إدخال اسم المهارة!");
    setLoading(true);
    await addDoc(skillsCollection, { name: currentSkill.name });
    setCurrentSkill({ id: null, name: "" });
    setIsEdit(false);

    Swal.fire({
      icon: "success",
      title: "عملية ناجحة",
      text: "تم إضافة المهارة بنجاح"
    });

    handleClose();
    fetchSkills();
  };

  const updateSkill = async () => {
    if (currentSkill.name.trim() === "") return alert("يرجى إدخال اسم المهارة!");
    setLoading(true);
    const skillDoc = doc(db, "skills", currentSkill.id);
    await updateDoc(skillDoc, { name: currentSkill.name });
    setCurrentSkill({ id: null, name: "" });
    setIsEdit(false);
    handleClose();
    fetchSkills();
  };

  const handleDelete = async (id) => {
    setLoading(true);
    const skillDoc = doc(db, 'skills', id);
    await deleteDoc(skillDoc);
    fetchSkills();
    setLoading(false);
  };

  return (
    <section className='myskills'>
      {loading && (
        <div className="loading-overlay">
          <div className="spinner"></div>
          <p>جاري التنفيذ...</p>
        </div>
      )}

      <div className="d-flex justify-content-between">
        <h3>إدارة المهارات</h3>
        <Button variant="success" onClick={handleShow}>
          إضافة مهارة جديدة
        </Button>
      </div>

      <Table className='mt-3' striped bordered>
        <thead>
          <tr>
            <th>رقم المهارة</th>
            <th>المهارة</th>
            <th>إجراء</th>
          </tr>
        </thead>
        <tbody>
          {skills.map((s, index) => (
            <tr key={s.id}>
              <td>{index + 1}</td>
              <td>{s.name}</td>
              <td>
                <Button
                  variant="success"
                  onClick={() => {
                    handleShow();
                    setCurrentSkill(s);
                    setIsEdit(true);
                  }}
                >
                  <i className="fa fa-edit"></i>
                </Button>{' '}
                <Button variant="danger" onClick={() => handleDelete(s.id)}>
                  <i className="fa fa-trash"></i>
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/* المودال لإضافة أو تعديل المهارة */}
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{isEdit ? "تعديل المهارة" : "إضافة مهارة جديدة"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <input
            type="text"
            className="form-control mb-2"
            placeholder="ادخل المهارة"
            value={currentSkill.name}
            onChange={(e) =>
              setCurrentSkill({ ...currentSkill, name: e.target.value })
            }
          />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            إلغاء
          </Button>
          <Button variant="primary" onClick={isEdit ? updateSkill : addSkill}>
            حفظ البيانات
          </Button>
        </Modal.Footer>
      </Modal>
    </section>
  );
};

export default IndexSkills;
